



























	
	import {
	    mapState,
	    mapMutations
	} from 'vuex'
	
    export default {
        data() {
            return {
                tobook:'',
				frombook: "",
                sendDate: {
                   from_bid:'',
                   
				   to_bid:''
                }
            }
        },
        
			
			onLoad:function(e){
					console.log("e"+e.bname+e);
					console.log(e);
					this.tobook=e.bname;
					//this.sendDate.receiver=e.bowner;
					this.sendDate.to_bid=e.bid;
				},
        
        
		computed: {
			...mapState(['header','data'])
			},
        methods: {
			post() {
				console.log("进入提交");
				
				//this.sendDate.tobook=this.tobook;
				//this.sendDate.sender=this.data.nickname;
				
					console.log(this.sendDate);
				 if(this.frombook ==''){
					 uni.showToast({
					 	title: '请补全信息',
					 	mask: false,
					 	duration: 1500
					 });
					 return;
				 }else {
					 uni.showLoading({
					 	title: '提交中',
					 	mask: false
					 });
					 uni.request({
					 	url: 'http://api.thinker.ink/v1/books/?search='+this.frombook,
					 	method: 'GET',
					 	
					 	success: res => {
							console.log(res);
							//console.log('from_bid='+res.data.data[0].bid);
                   if (res.data.count!=0) {  
					   this.sendDate.from_bid=res.data.data[0].bid;
					   console.log('sendDate终');
					   console.log(this.sendDate);
					 uni.request({
					 	url: 'https://api.thinker.ink/v1/books/applications/publish/',
					 	method: 'POST',
					 	data:this.sendDate,
						header:this.header,
					 	success: res => {
							console.log(res);
							uni.hideLoading();
							 if (res.statusCode === 201) {
							    uni.showToast({
							        title: "发布成功!"
							    });
							   
							    }else{
									uni.showToast({
									    title: "发布失败!"
									});
								}
							
							console.log(res);
							},
						
					 });
				 }	else{
					 uni.showToast({
					 	title: '您未拥有此书',
					 	mask: false,
					 	duration: 1500
					 });
				 }						
							
						},
					 	fail: () => {},
					 	complete: () => {}
					 });
					 
				 }
				
				}
           
            
        }
    }
